@font-face {
    font-family: "Audrey";
    src: local("audrey-normal-webfont"),
    url("./fonts/audrey-normal-webfont.woff") format("woff");
}
@font-face {
    font-family: "Jaceline";
    src: local("Jaceline"),
    url("./fonts/Jaceline.ttf") format("truetype");
}
h1 {
    font-family: 'Audrey', sans-serif;
    color: white;
    font-size: 300%;
    margin-top: -24.5%;
    font-weight: 100;
}
h2 {
    font-family: 'Audrey', sans-serif;
    font-size: 850%;
    color: #440000e0;
    margin-top: 30%;
    font-weight: 100;
}
h3 {
    font-family: 'Jaceline', cursive;
    color: white;
    font-size: 650%;
    font-weight: 100;
    margin-top: 0%;
    margin-bottom: 5%;
}

.recuadro_desk {
    position: relative;
    box-shadow: 8px 8px 22px 1px #1c1c1c66;
    backdrop-filter: blur(20px);
}

div.bg_text_01_desk {
    background-image: url("./imgs/text_01d.jpg");
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    margin-top: -0.5%;
}
div.bg_text_02_desk  { 
    background-image: url("./imgs/text_02d.jpg");
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
    display: flex;
    justify-content: center;
}

.recuadro_desk div.bg_01_desk {
    background-image: url("./imgs/recuadro_01.png");
    background-size: 100% 100%;
    -webkit-backdrop-filter: blur(20px);
    backdrop-filter: blur(20px);
    display: flex;
    justify-content: center;
    align-items: flex-end;
}
.recuadro_desk div.bg_02_desk {
    background-image: url("./imgs/recuadro_02.png");
    background-size: 100% 100%;
    -webkit-backdrop-filter: blur(20px);
    backdrop-filter: blur(20px);
    display: flex;
    justify-content: center;
}
.recuadro_desk div.bg_03_desk {
    background-image: url("./imgs/recuadro_03.png");
    background-size: 100% 100%;
    -webkit-backdrop-filter: blur(20px);
    backdrop-filter: blur(20px);
    display: flex;
    justify-content: center;
    align-items: flex-end;
}

.recuadro_desk a.bttn_01_desk {
    width: fit-content;
    height: fit-content;
}
a.bttn_text_02_desk {
    margin-top: 10%;
    width: fit-content;
    height: fit-content;
}
.recuadro_desk a.bttn_03_desk {
    width: fit-content;
    height: fit-content;
}

div.desktop_whitespace {
    width: 100%;
    padding: 0px;
    margin: 0px;
}

img.desktop_bottom_img {
    marginLeft: 0em;
}

div.img_container_desk {
    display: flex;
    overflow: hidden;
    width: 100%;
    justify-content: center;
    align-items: flex-start;
    position: fixed;
    top: 0;
    left: 0;
}

.img_02_desk, .img_03_desk, .img_04_desk, .img_05_desk {
    position: fixed;
    top: 0;
    left: 0;
}

h1.whitespace_child_desk {
    font-family: 'Audrey', sans-serif;
    color: #36f450;
    font-size: 300%;
    background: black;
    margin-top: 0%;
    font-weight: 100;
}

div.bg_image_08_desk {
    background-image: url("./imgs/image_08.png");
    background-size: 100% 100%;
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
    align-items: center;
}

div a.bttn_locations_01_desk {
    margin-top: 70%;
    width: fit-content;
    height: fit-content;
}

div a.bttn_locations_02_desk {
    margin-top: 34%;
    width: fit-content;
    height: fit-content;
}

div a.bttn_locations_03_desk {
    margin-top: 34%;
    width: fit-content;
    height: fit-content;
}

div.loading_wall_desk {
    width: 100%;
    height: 100%;
    background: white;
    z-index: 5000;
    position: fixed;
    top: 0em;
    left: 0em;
    transition: opacity 1.0s;
}
.wall-background_desk {
    filter: brightness(0.1);
    width: 100%;
}
.progress_wheel_desk {
    position: fixed;
    width: 10%;
    top: calc(40% - 5%);
    left: calc(50% - 5%);
    z-index: 2000;
}

div.desktop_counter {
    display: grid;
    grid-gap: 0px;
    margin-bottom: -10%;
    font-family: 'Montserrat', sans-serif;
    font-weight: 700;
    text-align: center;
    color: #62121F;
}

div.count_days, div.count_hours, div.count_minutes, div.count_secs {
    display: inline;
    width: 2.5em;
}
div.count_days {
    grid-column: 1;
}
div.count_hours {
    grid-column: 2;
}
div.count_minutes {
    grid-column: 3;
}
div.count_secs {
    grid-column: 4;
}


