.recuadro {
  position: relative;
  box-shadow: 8px 8px 22px 1px #1c1c1c66;
  backdrop-filter: blur(20px);
}

.recuadro div.bg_01 {
  background-image: url("./imgs/recuadro_01.png");
  background-size: 100% 100%;
  -webkit-backdrop-filter: blur(20px);
  backdrop-filter: blur(20px);
  display: flex;
  justify-content: center;
}
.recuadro a.bttn_01 {
  margin-top: 135%;
  width: fit-content;
  height: fit-content;
}

.recuadro div.bg_02 {
  background-image: url("./imgs/recuadro_02.png");
  background-size: 100% 100%;
  -webkit-backdrop-filter: blur(20px);
  backdrop-filter: blur(20px);
  display: flex;
  justify-content: center;
}

.recuadro div.bg_03 {
  background-image: url("./imgs/recuadro_03.png");
  background-size: 100% 100%;
  -webkit-backdrop-filter: blur(20px);
  backdrop-filter: blur(20px);
  display: flex;
  justify-content: center;
}
.recuadro a.bttn_03 {
  margin-top: 50%;
  width: fit-content;
  height: fit-content;
}

div.bg_text_02  { 
  background-image: url("./imgs/text_02.png");
  background-size: 100% 100%;
  display: flex;
  justify-content: center;
}
a.bttn_text_02 {
  margin-top: 73%;
  width: fit-content;
  height: fit-content;
}

div.mobile_whitespace {
  width: 100%;
  padding: 0px;
  margin: 0px;
}

img.mobile_bottom_img {
  marginLeft: 0em;
}

div.img_container {
  display: flex;
  overflow: hidden;
  width: 100%;
  justify-content: center;
  align-items: flex-start;
  position: fixed;
  top: 0;
  left: 0;
}

.img_02, .img_03, .img_04, .img_05 {
  position: fixed;
  top: 0;
  left: 0;
}

.img_05:after {
  background: rgba(0, 150, 0, 0.75);
}

h1.whitespace_child {
  font-family: 'Audrey', sans-serif;
  color: #36f450;
  font-size: 300%;
  background: black;
  margin-top: 0%;
  font-weight: 100;
}

div.bg_text_01 {
  background-image: url("./imgs/text_01.png");
    background-size: 100% 100%;
    display: flex;
    flex-direction: row;
    align-items: flex-end;
    justify-content: center;
}

div.bg_image_08 {
  background-image: url("./imgs/image_08.png");
  background-size: 100% 100%;
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
  align-items: center;
}

div a.bttn_locations_01 {
  margin-top: 70%;
  width: fit-content;
  height: fit-content;
}

div a.bttn_locations_02 {
  margin-top: 34%;
  width: fit-content;
  height: fit-content;
}

div a.bttn_locations_03 {
  margin-top: 34%;
  width: fit-content;
  height: fit-content;
}

div.loading_wall {
  width: 100%;
  height: 100%;
  background: white;
  z-index: 5000;
  position: fixed;
  top: 0em;
  left: 0em;
  transition: opacity 1.0s;
}
.wall-background {
  filter: brightness(0.1);
  width: 100%;
}
.progress_wheel {
  position: fixed;
  width: 10%;
  top: calc(40% - 5%);
  left: calc(50% - 5%);
  z-index: 2000;
}

div.mobile_counter {
    display: grid;
    grid-gap: 0px;
    margin-bottom: 5%;
    font-family: 'Montserrat', sans-serif;
    font-size: 150%;
    font-weight: 700;
    text-align: center;
    color: #62121F;
}

div.count_days, div.count_hours, div.count_minutes, div.count_secs {
  display: inline;
  width: 2.5em;
}
div.count_days {
  grid-column: 1;
}
div.count_hours {
  grid-column: 2;
}
div.count_minutes {
  grid-column: 3;
}
div.count_secs {
  grid-column: 4;
}


